<template>
  <div class="pagination">
    <el-pagination
      prev-text="上一页"
      next-text="下一页"
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="currentPage"
      :page-sizes="[2, 4, 6, 8]"
      :page-size="pageSize"
      layout="total, sizes, prev, pager, next, jumper"
      :total="total">
    </el-pagination>
  </div>
</template>

<script>
export default {
  name: 'CommunalPagination',
  props: {
    currentPage: {
      type: Number
    },
    pageSize: {
      type: Number
    },
    total: {
      type: Number
    }
  },
  methods: {
    handleSizeChange (val) {
      this.$emit('search', 1, val)
    },
    handleCurrentChange (val) {
      this.$emit('search', val, this.pageSize)
    }
  }
}
</script>

<style scoped>
  .pagination {
    text-align: right;
    margin-top: 25px;
  }
</style>



